import request from '@/utils/request'

// 
export function fetchCategories() {
    return request({
      url: '/categories',
      method: 'get'
    })
}
// 
export function fetchsubCategories() {
  return request({
    url: '/sub-categories',
    method: 'get'
  })
}
// 
export function fetchcategoryInfo(query) {
  return request({
    url: '/category-info',
    method: 'get',
    params: query
  })
}